var LoginContent = React.createClass({
    getLoginData: function(event) {
        var emailRegx = /^([\w-\.\+\-\_]+)@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.)|(([\w-]+\.)+))([a-zA-Z]{2,4})$/;
        if(this.state.userAccount != null){
            if (!this.state.userAccount.match(emailRegx)) {
                this.setState({
                    userAccountCheck: false,
                    userAccountErrMsg: "請輸入正確E-Mail帳號"
                });
                return;
            } else {
                this.setState({ userAccountCheck: true });
                document.getElementById("loginForm").submit();
            }
        }else{
            this.setState({
                userAccountCheck: false,
                userAccountErrMsg: "請輸入E-Mail帳號"
            });
        }

    },

    getInitialState: function() {
        return {
            userAccount: netwRespMessage.user,
            userAccountCheck: true,
            userAccountErrMsg: "",
            userPassword: "",
            response: netwRespMessage
        };
    },

    handleInputText: function(e) {
        var names = e.target.name,
            targetVal = e.target.value;

        switch (names) {
            case 'user':
                this.setState({ userAccount: targetVal });
                break;
            case 'pass':
                this.setState({userPassword: targetVal});
                break;
            default:
        }
    },

    checkUserError: function(){
        var className = '';
        if(!!this.state.userAccountCheck == false){
            className = "errMsg";
        }else{
            return ;
        }

        return className;
    },

    checkPwdError: function(){
        var className = '';
        if(!!this.state.response.Pwderro){
            className = "errMsg";
        }else{
            return ;
        }

        return className;
    },

    render: function() {
        return (
            <div className="login">
                <div className="title">
                    <h3>新蛋全球生活網會員登入</h3>
                </div>
                <div className="loginPage">
                    <form id="loginForm" action={nemReact.generateUrl("login")} method="post">
                        <ul className="loginContent">
                            <li>
                                <p className={this.checkUserError()}>
                                    <input type="Email" name="user" defaultValue={this.state.userAccount} placeholder="請輸入e-mail(新蛋帳號)" onChange={this.handleInputText}/>
                                    <span>{this.state.userAccountErrMsg}</span>
                                </p>
                                <p className={this.checkPwdError()}>
                                    <input type="password" name="pass" defaultValue={this.state.userPassword} placeholder="請輸入密碼(8-12碼數字或英文字母混合)" onChange={this.handleInputText}/>
                                    <span>{this.state.response.Pwderro}</span>
                                </p>
                                <a className="loginBtn" onClick={this.getLoginData}>登入</a>
                                <p className="forgetPwd">
                                    <a href={_netwWebURL + nemReact.generateUrl("forgetpass")}>忘記密碼?</a>
                                </p>
                                <a className="registerBtn" href={nemReact.generateUrl("signup")}>申請註冊</a>
                                <p className="desktopView" style={{
                                    display: 'none'
                                }}>
                                    <a href={_netwWebURL}>檢視桌上型電腦版</a>
                                </p>
                            </li>
                        </ul>
                        <input type="hidden" id="ratm" name="ratm" value={this.state.response.ratm}/>
                        <input type="hidden" id="type" name="type" value={this.state.response.type}/>
                        <input type="hidden" id="acty" name="acty" value={this.state.response.acty}/>
                        <input type="hidden" id="returnUrl" name="returnUrl" value={netwRespReturnUrl}/>
                    </form>
                </div>
            </div>
        );
    }
});

module.exports = LoginContent;



